import React, { useEffect, useRef } from 'react';

export default function CreatureAnimation() {
  const creatureRef = useRef(null);

  useEffect(() => {
    const script = document.createElement('script');
    script.type = 'module';
    script.innerHTML = `
      import { animate, createTimeline, createTimer, stagger, utils } from 'https://assets.codepen.io/1137/anime.esm.min.js';

      const creatureEl = document.getElementById('creature');
      const viewport = { w: window.innerWidth * .5, h: window.innerHeight * .5 };
      const cursor = { x: 0, y: 0 };
      const rows = 10;
      const grid = [rows, rows];
      const from = 'center';
      const scaleStagger = stagger([2, 5], { ease: 'inQuad', grid, from });
      const opacityStagger = stagger([1, .1], { grid, from });

      for (let i = 0; i < (rows * rows); i++) {
        creatureEl.appendChild(document.createElement('div'));
      }

      const particuleEls = creatureEl.querySelectorAll('div');

      utils.set(creatureEl, {
        width: rows * 10 + 'em',
        height: rows * 10 + 'em'
      });

      utils.set(particuleEls, {
        x: 0,
        y: 0,
        scale: scaleStagger,
        opacity: opacityStagger,
        background: stagger([80, 20], {
          grid,
          from,
          modifier: v => \`hsl(4, 70%, \${v}%)\`,
        }),
        boxShadow: stagger([8, 1], {
          grid,
          from,
          modifier: v => \`0px 0px \${utils.round(v, 0)}em 0px var(--red)\`,
        }),
        zIndex: stagger([rows * rows, 1], { grid, from, modifier: utils.round(0) }),
      });

      const pulse = () => {
        animate(particuleEls, {
          keyframes: [
            {
              scale: 5,
              opacity: 1,
              delay: stagger(90, { start: 1650, grid, from }),
              duration: 150,
            }, {
              scale: scaleStagger,
              opacity: opacityStagger,
              ease: 'inOutQuad',
              duration: 600
            }
          ],
        });
      }

      const mainLoop = createTimer({
        frameRate: 15,
        onUpdate: () => {
          animate(particuleEls, {
            x: cursor.x,
            y: cursor.y,
            delay: stagger(40, { grid, from }),
            duration: stagger(120, { start: 750, ease: 'inQuad', grid, from }),
            ease: 'inOut',
            composition: 'blend',
          });
        }
      });

      const autoMove = createTimeline()
      .add(cursor, {
        x: [-viewport.w * .45, viewport.w * .45],
        modifier: x => x + Math.sin(mainLoop.currentTime * .0007) * viewport.w * .5,
        duration: 3000,
        ease: 'inOutExpo',
        alternate: true,
        loop: true,
        onBegin: pulse,
        onLoop: pulse,
      }, 0)
      .add(cursor, {
        y: [-viewport.h * .45, viewport.h * .45],
        modifier: y => y + Math.cos(mainLoop.currentTime * .00012) * viewport.h * .5,
        duration: 1000,
        ease: 'inOutQuad',
        alternate: true,
        loop: true,
      }, 0);

      const manualMovementTimeout = createTimer({
        duration: 1500,
        onComplete: () => autoMove.play(),
      });

      const followPointer = e => {
        const event = e.type === 'touchmove' ? e.touches[0] : e;
        cursor.x = event.pageX - viewport.w;
        cursor.y = event.pageY - viewport.h;
        autoMove.pause();
        manualMovementTimeout.restart();
      }

      document.addEventListener('mousemove', followPointer);
      document.addEventListener('touchmove', followPointer);
    `;
    document.body.appendChild(script);

    return () => {
      document.body.removeChild(script);
    };
  }, []);

  return (
    <div id="creature-wrapper" style={wrapperStyle}>
      <div id="creature" ref={creatureRef} style={creatureStyle}></div>
    </div>
  );
}

const wrapperStyle = {
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  position: 'absolute',
  top: 0,
  left: 0,
  zIndex:100,
  // overflow: 'hidden',\
  width: '100%',
  height: '100%',
};

const creatureStyle = {
  fontSize: '.1vh',
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  flexWrap: 'wrap',
  width: '100em',
  height: '100em',
  zIndex:10
};

const style = document.createElement('style');
style.innerHTML = `
  body {
    position: absolute;
    width: 100%;
    height: 100%;
  }

  #creature div {
    transform-style: preserve-3d;
    position: relative;
    width: 4em;
    height: 4em;
    margin: 3em;
    border-radius: 2em;
    will-change: transform;
    mix-blend-mode: plus-lighter;
    background: var(--red);
  }
`;
document.head.appendChild(style);
